@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Long+Cang&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-size: 16px;
	font-family: 'Lato', sans-serif;
}

img {
	width: 100px;
}

main.space {
	padding: 0 20px;
	position: relative;
	width: 100%;
	height: 100vh;
	background: linear-gradient(to bottom, #11131d, #040e47);
	background-attachment: fixed;
	overflow: hidden;
	display: grid;
	grid-template: repeat(2, 1fr) / 1fr;
	grid-row-gap: 20px;
	row-gap: 20px;
}
.main-404,
.main-info {
	justify-self: center;
	width: 100%;
	position: relative;
}

@media screen and (min-width: 425px) {
	main.space {
		padding: 0;
	}
}

@media screen and (min-width: 900px) {
	main.space {
		grid-template: 1.5fr 1fr / 1fr;
	}
}

.main-404 {
	position: relative;
	align-self: flex-end;
	font-family: 'Long Cang', cursive;
	font-size: 9em;
	font-weight: bold;
	display: grid;
	grid-template: 1fr / repeat(3, 1fr);
}
.main-404::before {
	font-family: 'Lato', sans-serif;
	content: 'ERROR';
	position: absolute;
	bottom: calc(100% - 20px);
	left: 50%;
	transform: translateX(-50%);
	font-size: 4rem;
	font-weight: 700;
	color: rgb(148, 48, 48);
	z-index: 80;
}
.main-404 p {
	text-shadow: 0px 0px 70px rgba(255, 187, 0, 0.7);
	background: linear-gradient(
		to bottom right,
		rgb(250, 28, 176),
		rgb(241, 27, 27)
	);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	-webkit-animation: latido linear infinite 1s alternate;
	        animation: latido linear infinite 1s alternate;
}
@-webkit-keyframes latido {
	from {
		transform: scale(1);
	}
	25% {
		transform: scale(0.9);
	}
	50% {
		transform: scale(1.2);
	}
	75% {
		transform: scale(0.8);
	}
	to {
		transform: scale(1);
	}
}
@keyframes latido {
	from {
		transform: scale(1);
	}
	25% {
		transform: scale(0.9);
	}
	50% {
		transform: scale(1.2);
	}
	75% {
		transform: scale(0.8);
	}
	to {
		transform: scale(1);
	}
}
p.right {
	padding-left: 10px;
}
.main-404 .moon {
	position: relative;
	z-index: 100;
	-webkit-animation: linear rotateMoon infinite 15s;
	        animation: linear rotateMoon infinite 15s;
	justify-self: center;
}
@-webkit-keyframes rotateMoon {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
@keyframes rotateMoon {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

@media screen and (min-width: 375px) {
	.main-404 {
		font-size: 10em;
	}
	p.right {
		padding-left: 8px;
	}
}

@media screen and (min-width: 425px) {
	.main-404 {
		font-size: 14em;
		width: 425px;
		margin: 0 auto;
	}
	p.left {
		padding-left: 15px;
	}
	.container-moon {
		padding-left: 10px;
	}
	img.moon {
		width: 120px;
	}
	.main-404::before {
		bottom: calc(100% - 40px);
		font-size: 5rem;
		transform: translateX(-47%);
	}
}
@media screen and (min-width: 600px) {
	p.left {
		padding-left: 10px;
	}
	img.moon {
		width: 150px;
	}
}

.main-info {
	width: 90%;
	max-width: 900px;
	position: relative;
	text-align: center;
	z-index: 90;
}
.main-info h2 {
	color: #eee;
	font-size: 1.8rem;
}
.main-info p {
	color: #fff;
	font-size: 1.5rem;
	margin: 20px 0;
	color: rgb(230, 204, 255);
}
.main-info a {
	position: relative;
	display: inline-block;
	color: #2196f3;
	padding: 20px 30px;
	border-radius: 5px;
	font-size: 30px;
	letter-spacing: 2px;
	text-transform: uppercase;
	text-decoration: none;
	box-shadow: 0 20px 50px rgba(0, 0, 0, 0.5);
	overflow: hidden;
	transition: 0.2s;
}
.main-info a:hover {
	color: #193b5a;
	background: #2196f3;
	box-shadow: 0 0 10px #2196f3, 0 0 40px #2196f3, 0 0 80px #2196f3;
	transition-delay: 1s;
}
.main-info a span {
	position: absolute;
	display: block;
}
.main-info a span:nth-child(1) {
	top: 0;
	left: -100%;
	width: 100%;
	height: 2px;
	background: linear-gradient(90deg, transparent, #2196f3);
}
.main-info a:hover span:nth-child(1) {
	left: 100%;
	transition: 1s;
}
.main-info a span:nth-child(2) {
	right: 0;
	top: -100%;
	width: 2px;
	height: 100%;
	background: linear-gradient(180deg, transparent, #2196f3);
}
.main-info a:hover span:nth-child(2) {
	top: 100%;
	transition: 1s;
	transition-delay: 0.25s;
}
.main-info a span:nth-child(3) {
	right: -100%;
	bottom: 0;
	width: 100%;
	height: 2px;
	background: linear-gradient(270deg, transparent, #2196f3);
}
.main-info a:hover span:nth-child(3) {
	right: 100%;
	transition: 1s;
	transition-delay: 0.5s;
}
.main-info a span:nth-child(4) {
	left: 0;
	bottom: -100%;
	width: 2px;
	height: 100%;
	background: linear-gradient(360deg, transparent, #2196f3);
}
.main-info a:hover span:nth-child(4) {
	bottom: 100%;
	transition: 1s;
	transition-delay: 0.75s;
}

@media screen and (min-width: 425px) {
	.main-info {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.main-info p,
	.main-info a {
		width: 350px;
	}
	.main-info p {
		margin: 25px 0;
	}
}

.container-astronaut {
	position: fixed;
}
.container-astronaut {
	top: 300px;
	left: 50px;
	transition-property: top, left;
	transition-duration: 4s;
	transition-timing-function: ease-in;
	-webkit-animation: moveAstronaut linear infinite 3s alternate;
	        animation: moveAstronaut linear infinite 3s alternate;
	z-index: 90;
}
.container-astronaut::before {
	display: none;
	content: attr(message);
	position: absolute;
	width: 120px;
	height: 60px;
	text-align: center;
	font-size: 16px;
	bottom: calc(100% - 10px);
	left: calc(100% - 60px);
	-webkit-clip-path: polygon(
		0% 0%,
		100% 0%,
		100% 75%,
		48% 76%,
		21% 100%,
		26% 75%,
		0% 75%
	);
	        clip-path: polygon(
		0% 0%,
		100% 0%,
		100% 75%,
		48% 76%,
		21% 100%,
		26% 75%,
		0% 75%
	);
	padding: 10px;
	background-color: rgb(255, 255, 255);
	color: rgb(40, 18, 61);
	border-radius: 5px;
	transition: display 0.3s linear;
}
.container-astronaut.active::before {
	display: block;
}
@-webkit-keyframes moveAstronaut {
	from {
		transform: translate(0) rotate(5deg);
	}
	50% {
		transform: translate(3px, 4px) rotate(-8deg);
	}
	to {
		transform: translate(6px, -5px) rotate(2deg);
	}
}
@keyframes moveAstronaut {
	from {
		transform: translate(0) rotate(5deg);
	}
	50% {
		transform: translate(3px, 4px) rotate(-8deg);
	}
	to {
		transform: translate(6px, -5px) rotate(2deg);
	}
}

@media screen and (min-width: 600px) {
	img.astronaut {
		width: 120px;
		cursor: pointer;
	}
}

main.space i {
	position: absolute;
	background: #fff;
	border-radius: 50%;
	/* transition-property: top, left;
	transition-duration: 1s;
	transition-timing-function: linear; */
	-webkit-animation: animate linear infinite forwards;
	        animation: animate linear infinite forwards;
}
@-webkit-keyframes animate {
	0% {
		opacity: 0;
		transform: translateY(0);
	}
	10%,
	90% {
		opacity: 1;
	}
	100% {
		opacity: 0;
		transform: translateY(-100px);
	}
}
@keyframes animate {
	0% {
		opacity: 0;
		transform: translateY(0);
	}
	10%,
	90% {
		opacity: 1;
	}
	100% {
		opacity: 0;
		transform: translateY(-100px);
	}
}

